import React, { useState } from "react";
import axios from "axios";
import alert from "../assets/alert.png";
import Success from "./success";

export default function DeleteActivity(props) {
  const deleteItems = async () => {
    let url = `https://todo.api.devcode.gethired.id/todo-items/${props.id}`;

    try {
      await axios.delete(url);
      props.getArtikel();
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <React.Fragment>
      <div
        onClick={() => {
          props.setHapus(false);
        }}
        className="bg-black bg-opacity-50 absolute inset-0 h-screen flex justify-center items-center"
      >
        <div data-cy="modal-delete" className="bg-white rounded-lg ">
          <div className="flex flex-col items-center justify-center py-10 px-24">
            <div className="">
              <img data-cy="modal-delete-icon" src={alert} alt="" className="h-28" />
            </div>
            <p data-cy="modal-delete-title" className="popin text-2xl text-center my-16">
              Apakah anda yakin menghapus item <br />
              <span className="popin text-2xl capitalize font-semibold">
                "{props.title}?"
              </span>
            </p>
            <div className="flex space-x-7">
              <button
              data-cy="modal-delete-cancel-button"
                onClick={() => {
                  props.setHapus(false);
                }}
                className="popin bg-gray-200 text-2xl px-12 rounded-full text-gray-400 py-4 font-semibold "
              >
                Batal
              </button>
              <button
              data-cy="modal-delete-confirm-button"

                onClick={() => {
                  deleteItems();
                  props.setHapus(false);
                }}
                className="popin bg-red-500 text-2xl px-12 rounded-full text-white py-4 font-semibold "
              >
                Hapus
              </button>
            </div>
          </div>
        </div>
      </div>
      
    </React.Fragment>
  );
}
