import React from "react";
import alert from "../assets/alert.png";
import axios from "axios";

export default function DeletePopUp(props) {
  const deleteHandle = async () => {
    let url = `https://todo.api.devcode.gethired.id/activity-groups/${props.id}`;
    try {
      await axios.delete(url);
      props.setSuccess(true);
      props.getArtikel();
    } catch (err) {
      console.log(err);
      props.setFailed(true);
    }
  };
  let vh = "";
  if (props.artikels.length <= 4) {
    vh = "126";
  } else {
    vh = "161";
  }
  return (
    <React.Fragment>
      <div
        onClick={() => {
          props.setModal(false);
        }}
        style={{ height: `${vh}vh` }}
        className="bg-black bg-opacity-50 absolute inset-0  flex justify-center items-center"
      >
        <div data-cy="modal-delete" className="bg-white rounded-lg">
          <div className="flex flex-col items-center justify-center py-10 px-24">
            <div className="">
              <img data-cy="modal-delete-icon" src={alert} alt="" className="h-28" />
            </div>
            <p className="popin text-2xl text-center my-16">
              Apakah anda yakin menghapus activity <br />
              <span className="popin text-2xl font-semibold">
                "{props.title}?"
              </span>
            </p>
            <div className="flex space-x-7">
              <button
                data-cy="modal-delete-cancel-button"
                onClick={() => {
                  props.setModal(false);
                }}
                className="popin bg-gray-200 text-2xl px-12 rounded-full text-gray-400 py-4 font-semibold "
              >
                Batal
              </button>
              <button
                data-cy="modal-delete-confirm-button"
                onClick={() => {
                  deleteHandle();
                  props.setModal(false);
                  props.setSuccess(true);
                }}
                className="popin bg-red-500 text-2xl px-12 rounded-full text-white py-4 font-semibold "
              >
                Hapus
              </button>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
