import React from "react";

export default function Success() {
  return (
    <React.Fragment>
      <div className="bg-black bg-opacity-50 h-screen flex items-center justify-center absolute inset-0">
        <div
        data-cy="modal-information"
          style={{ width: "400px" }}
          className="flex bg-white py-2 rounded-md shadow-md mt-8 mx-8 px-5 space-x-6"
        >
          <svg
          data-cy="modal-information-icon"
            xmlns="http://www.w3.org/2000/svg"
            className="h-7 w-7 text-green-500"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
            />
          </svg>
          <p data-cy="modal-information-title" className="text-xl capitalize popin font-semibold">
            activity berhasil di hapus
          </p>
        </div>
      </div>
    </React.Fragment>
  );
}
