import React from "react";

export default function PriorityUpdate(props) {
  return (
    <React.Fragment>
      <div className="absolute flex flex-col border rounded-lg bg-white">
        <button
          data-cy="modal-add-priority-item"
          onClick={() => {
            props.setSohigh(true);
            props.setHigh(false);
            props.setMedium(false);
            props.setLow(false);
            props.setVeryLow(false);
          }}
          className="flex items-center justify-between py-3 px-3 border border-l-0 border-r-0 border-t-0 w-52"
          type="button"
        >
          <div className="flex space-x-6">
            <div className="h-3 w-3 mt-2 bg-red-500 rounded-full"></div>
            <p className="text-xl popin capitalize">very-high</p>
          </div>
          {props.soHigh ? (
            <React.Fragment>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          data-cy="modal-add-priority-item"
          onClick={() => {
            props.setSohigh(false);
            props.setMedium(false);
            props.setLow(false);
            props.setVeryLow(false);
            props.setHigh(true);
          }}
          className="flex py-3 items-center justify-between px-3 border border-l-0 border-r-0 border-t-0 w-52"
          type="button"
        >
          <div className="flex space-x-6">
            <div className="h-3 w-3 mt-2 bg-yellow-500 rounded-full"></div>
            <p className="text-xl popin capitalize">high</p>
          </div>
          {props.high ? (
            <React.Fragment>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          data-cy="modal-add-priority-item"
          onClick={() => {
            props.setSohigh(false);
            props.setHigh(false);
            props.setLow(false);
            props.setVeryLow(false);
            props.setMedium(true);
          }}
          className="flex py-3 px-3 items-center justify-between border border-l-0 border-r-0 border-t-0 w-52"
          type="button"
        >
          <div className="flex space-x-6">
            <div className="h-3 w-3 mt-2 bg-green-500 rounded-full"></div>
            <p className="text-xl popin capitalize">medium</p>
          </div>
          {props.medium ? (
            <React.Fragment>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          data-cy="modal-add-priority-item"
          onClick={() => {
            props.setSohigh(false);
            props.setHigh(false);
            props.setMedium(false);
            props.setVeryLow(false);
            props.setLow(true);
          }}
          className="flex py-3 px-3 border items-center justify-between border-l-0 border-r-0 border-t-0 w-52"
          type="button"
        >
          <div className="flex space-x-6">
            <div className="h-3 w-3 mt-2 bg-blue-500 rounded-full"></div>
            <p className="text-xl popin capitalize">low</p>
          </div>
          {props.low ? (
            <React.Fragment>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          data-cy="modal-add-priority-item"
          onClick={() => {
            props.setSohigh(false);
            props.setHigh(false);
            props.setMedium(false);
            props.setLow(false);
            props.setVeryLow(true);
          }}
          className="flex py-3 px-3 border items-center justify-between border-l-0 border-r-0 border-t-0 w-52"
          type="button"
        >
          <div className="flex space-x-6">
            <div className="h-3 w-3 mt-2 bg-purple-700 rounded-full"></div>
            <p className="text-xl popin capitalize">very-low</p>
          </div>
          {props.veryLow ? (
            <React.Fragment>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
      </div>
    </React.Fragment>
  );
}
