import axios from "axios";
import React, { useState } from "react";
import { useHistory, useParams } from "react-router";
import Sort from "./sort";

export default function Title(props) {
  let { id } = useParams();
  const [values, setValues] = React.useState({
    title: "",
  });
  let history = useHistory();
  const getDetail = async (e) => {
    let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
    try {
      let respon = await axios.get(url);
      setValues((values) => ({
        ...values,
        title: respon.data.title,
      }));
    } catch (err) {
      console.log(err);
    }
  };

  const handleChange = async (e) => {
    let key = e.target.name;
    let value = e.target.value;

    setValues((values) => ({
      ...values,
      [key]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;

    try {
      await axios.patch(url, values);
    } catch (err) {
      console.log(err);
    }
  };
  const [focus, setFocus] = React.useState(false);

  React.useEffect(() => {
    getDetail();
  }, []);
  return (
    <React.Fragment>
      <div className="flex popin items-center justify-center px-72 py-7">
        <div className="flex space-x-4">
          <svg
          data-cy="todo-back-button"
            onClick={() => {
              history.push("/");
            }}
            data-cy="todo-back-button"
            xmlns="http://www.w3.org/2000/svg"
            className="h-12 w-12 mt-10 cursor-pointer"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M15 19l-7-7 7-7"
            />
          </svg>
          <form
            action=""
            onChange={handleSubmit}
            onBlur={handleSubmit}
            onSubmit={handleSubmit}
          >
            {focus ? (
              <React.Fragment>
                <input
                  data-cy="todo-title"
                  autoFocus
                  style={{ width: "390px" }}
                  value={values.title}
                  onChange={handleChange}
                  type="text"
                  name="title"
                  className="font-bold capitalize mt-10 mr-4 text-4xl outline-none bg-gray-50 border border-b-2 border-l-0 border-r-0 border-t-0 focus:border-blue-450"
                />
              </React.Fragment>
            ) : (
              <React.Fragment>
                <h1
                  data-cy="todo-title"
                  onClick={() => {
                    setFocus(true);
                  }}
                  className="font-bold w-96 capitalize mt-10 text-4xl outline-none focus:border-blue-450"
                >
                  {values.title}
                </h1>
              </React.Fragment>
            )}
          </form>
          <svg
          data-cy="todo-title-edit-button"
            onClick={() => {
              setFocus(!focus);
            }}
            data-cy="todo-title-edit-button"
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 text-gray-400 cursor-pointer relative top-14"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
            />
          </svg>
        </div>

        <div style={{ marginLeft: "54%" }} className="flex mt-5 space-x-6">
          <button
            data-cy="todo-sort-button"
            type="button"
            onClick={() => {
              props.setSort(!props.sort);
            }}
            className="w-16 h-16 bg-gray-50 cursor-pointer border rounded-full py-4 px-3"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-9 w-9 text-gray-400"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M7 16V4m0 0L3 8m4-4l4 4m6 0v12m0 0l4-4m-4 4l-4-4"
              />
            </svg>
          </button>

          <div
            onClick={() => {
              return props.setModal(true);
            }}
            data-cy="activity-add-button"
            className=" flex space-x-3 items-center cursor-pointer justify-between px-6 bg-blue-450 col-start-5 h-16 w-48 rounded-full"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-9 w-9 stroke-2 text-white"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M12 4v16m8-8H4"
              />
            </svg>
            <p className="text-2xl font-semibold text-white ">Tambah</p>
          </div>
          <div className="relative right-96 top-20" style={{}}>
            {props.sort ? (
              <React.Fragment>
                <Sort
                  reverse={props.reverse}
                  latest={props.latest}
                  newest={props.newest}
                  setReverse={props.setReverse}
                  getNewest={props.getNewest}
                  setNewest={props.setNewest}
                  setLatest={props.setLatest}
                  getLatest={props.getLatest}
                  getReverse={props.getReverse}
                  getAz={props.getAz}
                  sortTitle={props.sortTitle}
                  setSortTitle={props.setSortTitle}
                ></Sort>
              </React.Fragment>
            ) : (
              ""
            )}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
