import React from "react";
import { useState } from "react/cjs/react.development";
import axios from "axios";

export default function Check(props) {
  const [values, setValues] = useState({
    title: props.title,
    is_active: props.active,
    priority: props.priority,
    _comment: "list of priority is : very-high, high, normal, low, very-low",
  });
  
  const handleDone = async (e) => {
    values.is_active = values.is_active === 1 ? 0 :1
    handleSubmit();
  };

  const handleSubmit = async (e) => {
    let url = `https://todo.api.devcode.gethired.id/todo-items/${props.id}`;
    try {
      await axios.patch(url, values);
      props.getArtikel()
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <React.Fragment>
      <div className="item-checkbox relative bottom-7 ">
        <form onClick={handleSubmit} >
          <input
            data-cy="todo-item-checkbox"
            checked={values.is_active === 1 ? true : false}
            onChange={handleDone}
            value={values.is_active}
            type="checkbox"
            className="input relative top-7 opacity-0"
            name=""
            id=""
          />
          <label className="item-checkbox-content  cursor-pointer">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6 svg text-white"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M5 13l4 4L19 7"
              />
            </svg>
          </label>
        </form>
      </div>
    </React.Fragment>
  );
}
