import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSortAlphaDownAlt,
  faSortAlphaUp,
  faSortAmountDown,
  faSortAmountUp,
} from "@fortawesome/free-solid-svg-icons";
import React from "react";

export default function Sort(props) {
  return (
    <React.Fragment>
      <div
        data-cy="sort-selection"
        className="absolute rounded-lg border w-64 bg-white shadow-md"
      >
        <button
          onClick={() => {
            props.setSortTitle(false);
            props.setReverse(false);
            props.setLatest(false);
            props.setNewest(true);
            props.getNewest();
          }}
          type="button"
          className="border-b w-full hover:bg-gray-100 cursor-pointer px-5 flex space-x-4 py-3"
        >
          <div className="">
            <FontAwesomeIcon
              data-cy="sort-selection-icon"
              className="text-blue-450"
              icon={faSortAmountDown}
            />
          </div>
          <p data-cy="sort-selection-title" className="capitalize text-xl pr-8">
            terbaru
          </p>
          {props.newest ? (
            <React.Fragment>
              <svg
                data-cy="sort-selection-selected"
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          onClick={() => {
            props.setSortTitle(false);
            props.setReverse(false);
            props.setNewest(false);
            props.setLatest(true);
            props.getLatest();
          }}
          type="button"
          className="border-b w-full hover:bg-gray-100 cursor-pointer px-5 flex space-x-4 py-3"
        >
          <div className="">
            <FontAwesomeIcon
              data-cy="sort-selection-icon"
              className="text-blue-450"
              icon={faSortAmountUp}
            />
          </div>
          <p data-cy="sort-selection-title" className="capitalize text-xl pr-8">
            terlama
          </p>
          {props.latest ? (
            <React.Fragment>
              <svg
                data-cy="sort-selection-selected"
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          onClick={() => {
            props.setNewest(false);
            props.setLatest(false);
            props.setSortTitle(true);
            props.setReverse(false);
            props.getAz();
          }}
          type="button"
          className="border-b w-full hover:bg-gray-100 cursor-pointer px-5 flex space-x-4 py-3"
        >
          <div className="">
            <FontAwesomeIcon
              data-cy="sort-selection-icon"
              className="text-blue-450"
              icon={faSortAlphaUp}
              size="1x"
            />
          </div>
          <p
            data-cy="sort-selection-title"
            className="capitalize text-xl pr-20"
          >
            A-Z
          </p>
          {props.sortTitle ? (
            <React.Fragment>
              <svg
                data-cy="sort-selection-selected"
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          onClick={() => {
            props.setNewest(false);
            props.setLatest(false);
            props.setSortTitle(false);
            props.setReverse(true);
            props.getReverse();
          }}
          type="button"
          className="border-b w-full hover:bg-gray-100 cursor-pointer px-5 flex space-x-4 py-3"
        >
          <div className="">
            <FontAwesomeIcon
              data-cy="sort-selection-icon"
              icon={faSortAlphaDownAlt}
              className="text-blue-450"
            />
          </div>
          <p
            data-cy="sort-selection-title"
            className="capitalize text-xl pr-20"
          >
            Z-A
          </p>
          {props.reverse ? (
            <React.Fragment>
              <svg
                data-cy="sort-selection-selected"
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </React.Fragment>
          ) : (
            ""
          )}
        </button>
        <button
          type="button"
          className="border-b w-full hover:bg-gray-100 cursor-pointer px-5 flex space-x-4 py-3"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="h-5 w-5 text-blue-450 mt-1 stroke-2"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M7 16V4m0 0L3 8m4-4l4 4m6 0v12m0 0l4-4m-4 4l-4-4"
            />
          </svg>
          <p data-cy="sort-selection-title" className="capitalize text-xl pr-8">
            belum selesai
          </p>
        </button>
      </div>
    </React.Fragment>
  );
}
