import axios from "axios";
import React from "react";
import { useState } from "react/cjs/react.development";
import PriorityUpdate from "./priorityUpdate";

export default function UpdateActivity(props) {
  const [loading, setLoading] = useState(false);
  const [disableButton, setDisableButton] = useState(true);
  const [soHigh, setSohigh] = useState(false);
  const [high, setHigh] = useState(false);
  const [medium, setMedium] = useState(false);
  const [low, setLow] = useState(false);
  const [veryLow, setVeryLow] = useState(false);
  const [show, setShow] = useState(false);

  const [values, setValues] = useState({
    title: "",
    is_active: props.active,
    priority: "",
    _comment: "list of priority is : very-high, high, normal, low, very-low",
  });
  let valuePriority = values.priority;

  const getDetail = async () => {
    let url = `https://todo.api.devcode.gethired.id/todo-items/${props.id}`;

    try {
      let respon = await axios.get(url);
      setValues((values) => ({
        ...values,
        title: respon.data.title,
        is_active: respon.data.is_active,
        priority: respon.data.priority,
        _comment: respon.data._comment,
      }));
    } catch (err) {
      console.log(err);
    }
  };
  const handleChange = async (e) => {
    let key = e.target.name;
    let value = e.target.value;

    setValues((values) => ({
      ...values,
      [key]: value,
    }));

    if (key === "title") {
      if (value === "") {
        setDisableButton(true);
      } else {
        setDisableButton(false);
      }
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    let url = `https://todo.api.devcode.gethired.id/todo-items/${props.id}`;
    setLoading(true);

    try {
      await axios.patch(url, values);
      setLoading(false);
      props.getArtikel();
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  };

  if (soHigh) {
    values.priority = "very-high";
  } else if (high) {
    values.priority = "high";
  } else if (medium) {
    values.priority = "normal";
  } else if (low) {
    values.priority = "low";
  } else if (veryLow) {
    values.priority = "very-low";
  } else {
    values.priority = values.priority;
  }

  let circle = "";
  if (valuePriority === "very-high") {
    circle = "bg-red-500";
  } else if (valuePriority === "high") {
    circle = "bg-yellow-500";
  } else if (valuePriority === "normal") {
    circle = "bg-green-500";
  } else if (valuePriority === "low") {
    circle = "bg-blue-500";
  } else {
    circle = "bg-purple-700";
  }
  React.useEffect(() => {
    getDetail();
  }, []);
  return (
    <React.Fragment>
      <div className="bg-black bg-opacity-50  absolute inset-0 flex justify-center items-center">
        <div
          data-cy="modal-add"
          className="bg-white rounded-lg animate__fadeInDown"
        >
          <div className="px-10">
            <div className="flex items-center pt-10 pb-4 space-x-96 justify-between">
              <h1
                data-cy="modal-add-title"
                className="text-3xl popin font-semibold capitalize"
              >
                edit list item
              </h1>
              <svg
                data-cy="modal-add-close-button"
                onClick={() => {
                  props.setUpdate(false);
                }}
                xmlns="http://www.w3.org/2000/svg"
                className="h-6 w-6 cursor-pointer"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </div>
            <hr />
            <div className="mt-14 pb-10">
              <form onSubmit={handleSubmit} action="">
                <div className="grid gap-1">
                  <label
                    data-cy="modal-add-name-title"
                    htmlFor=""
                    className="uppercase text-xl font-semibold popin"
                  >
                    nama list item
                  </label>
                  <input
                    data-cy="modal-add-name-input"
                    name="title"
                    onChange={handleChange}
                    value={values.title}
                    placeholder="Tambahkan nama activity"
                    type="text"
                    className="border outline-none h-12 rounded-lg border-gray-300 px-3 popin focus:border-blue-500"
                  />
                </div>
                <div className="grid gap-1 pt-12">
                  <label
                    data-cy="modal-add-priority-title"
                    htmlFor=""
                    className="capitalize text-xl font-semibold  popin"
                  >
                    priority
                  </label>
                  <button
                    data-cy="modal-add-priority-dropdown"
                    name="priority"
                    onChange={handleChange}
                    onClick={(e) => {
                      setShow(!show);
                    }}
                    className="flex border py-3 px-3 items-center justify-between rounded-lg w-52"
                    type="button"
                  >
                    <div className="flex space-x-6">
                      <div
                        className={`h-3 w-3 mt-2 ${circle} rounded-full`}
                      ></div>
                      <p className="text-xl popin capitalize">
                        {values.priority}
                      </p>
                    </div>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      className="h-6 w-6"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 9l-7 7-7-7"
                      />
                    </svg>
                  </button>
                  <div className="relative">
                    {show ? (
                      <React.Fragment>
                        <PriorityUpdate
                          soHigh={soHigh}
                          high={high}
                          medium={medium}
                          low={low}
                          veryLow={veryLow}
                          setSohigh={setSohigh}
                          setHigh={setHigh}
                          setMedium={setMedium}
                          setLow={setLow}
                          setVeryLow={setVeryLow}
                        ></PriorityUpdate>
                      </React.Fragment>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
                <hr />
                <div className="flex items-end justify-end py-10 px-10">
                  <button
                    data-cy="modal-add-save-button"
                    disabled={disableButton}
                    type="submit"
                    className={` ${
                      disableButton
                        ? "bg-blue-300 cursor-not-allowed"
                        : "bg-blue-500"
                    } text-white px-10 py-4 text-2xl popin font-semibold rounded-full`}
                  >
                    {loading ? "loading..." : "Simpan"}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
