import React, { useState } from "react";
import { useHistory } from "react-router";
import DeletePopUp from "../../popup/delete";
import Success from "../../popup/success";

export default function List(props) {
  const [modal, setModal] = React.useState(false);
  const [success, setSuccess] = useState(false);
  const [failed, setFailed] = useState(false);

  const date = new Date(props.date);
  let month = [
    "januari",
    "februari",
    "maret",
    "april",
    "mei",
    "juni",
    "juli",
    "agustus",
    "september",
    "oktober",
    "november",
    "desember",
  ];
  let monthName = month[date.getMonth()];
  let history = useHistory();
  return (
    <React.Fragment>
      <div data-cy="activity-item" className="bg-white rounded-xl shadow-md">
        <h1
          data-cy="activity-item-title"
          onClick={() => {
            history.push(`/activity/${props.id}`);
          }}
          className="text-3xl capitalize font-bold popin cursor-pointer pt-9 px-12 h-60"
        >
          {props.title}
        </h1>

        <div className="flex items-center justify-between pb-9 px-12">
          <p data-cy="activity-item-date" className="text-xl capitalize text-gray-500">
            {date.getDate()} {monthName} {date.getFullYear()}
          </p>
          <svg
            data-cy="activity-item-delete-button"
            onClick={() => {
              setModal(true);
            }}
            xmlns="http://www.w3.org/2000/svg"
            className="h-6 w-6 text-gray-500 cursor-pointer"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
            />
          </svg>
        </div>
      </div>
      {modal ? (
        <React.Fragment>
          <DeletePopUp
            success={success}
            setSuccess={setSuccess}
            failed={failed}
            setFailed={setFailed}
            artikels={props.artikels}
            modal={modal}
            title={props.title}
            setModal={setModal}
            getArtikel={props.getArtikel}
            id={props.id}
          ></DeletePopUp>
        </React.Fragment>
      ) : (
        ""
      )}
      {success ? (
        <React.Fragment>
          <Success></Success>
        </React.Fragment>
      ) : (
        ""
      )}
    </React.Fragment>
  );
}
