import React, { useState } from "react";
import cowo from "../../assets/laki.png";
import Check from "./chek";
import DeleteActivity from "../../popup/deleteActivity";
import UpdateActivity from "../../popup/updateAct";
import { useHistory, useParams } from "react-router";
import axios from "axios";

export default function List(props) {
  const [selesai, setSelesai] = useState(false);

  const [update, setUpdate] = React.useState(false);
  const [hapus, setHapus] = React.useState(false);

  let { id } = useParams();
  const [values, setValues] = React.useState({
    title: "",
  });
  let history = useHistory();
  const getDetail = async (e) => {
    let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
    try {
      let respon = await axios.get(url);
      setValues((values) => ({
        ...values,
        title: respon.data.title,
      }));
    } catch (err) {
      console.log(err);
    }
  };


  React.useEffect(() => {
    getDetail();
  }, []);

  let circle = "";
  let priority = props.priority;
  if (priority === "very-high") {
    circle = "bg-red-500";
  } else if (priority === "high") {
    circle = "bg-yellow-500";
  } else if (priority === "normal") {
    circle = "bg-green-500";
  } else if (priority === "low") {
    circle = "bg-blue-500";
  } else {
    circle = "bg-purple-500";
  }
  return (
    <React.Fragment>
      <div
        data-cy="todo-item"
        onClick={() => {
          props.setSort(false);
        }}
        className="flex bg-white rounded-lg mb-8 shadow-md items-center justify-between pt-7 px-10"
      >
        <div className="flex space-x-4">
          <Check
            selesai={selesai}
            setSelesai={setSelesai}
            active={props.active}
            title={props.title}
            priority={props.priority}
            id={props.id}
            getArtikel={props.getArtikel}
          ></Check>
          <div data-cy="todo-item-priority-indicator" className={`h-4 w-4 ${circle} mt-1 rounded-full`}></div>
          <p
            data-cy="todo-item-title"
            className={`text-xl capitalize popin ${props.active === 1? "line-through text-gray-400":""} text-gray-500"`}
          >
            {props.title}
          </p>
          <div
            data-cy="todo-item-edit-button"
            className=""
            onClick={() => {
              setUpdate(true);
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-5 w-5 text-gray-500 mt-1 cursor-pointer"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
              />
            </svg>
          </div>
        </div>
        <svg
        data-cy="todo-item-delete-button"
          onClick={() => {
            setHapus(true);
          }}
          xmlns="http://www.w3.org/2000/svg"
          className="h-6 w-6 mb-6 text-gray-500 cursor-pointer"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
          />
        </svg>
      </div>
      {hapus ? (
        <React.Fragment>
          <DeleteActivity
            getArtikel={props.getArtikel}
            id={props.id}
            title={props.title}
            setHapus={setHapus}
            hapus={hapus}
          ></DeleteActivity>
        </React.Fragment>
      ) : (
        ""
      )}

      {update ? (
        <React.Fragment>
          <UpdateActivity
            id={props.id}
            update={update}
            setUpdate={setUpdate}
            getArtikel={props.getArtikel}
          ></UpdateActivity>
        </React.Fragment>
      ) : (
        ""
      )}
    </React.Fragment>
  );
}
