import React from "react";
import List from "./list";
import Title from "./title";
import cowo from "../../assets/laki.png";
import axios from "axios";

export default function Dashboard() {
  const [artikels, setArtikels] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const getArtikel = async () => {
    setLoading(true);
    try {
      const url = `https://todo.api.devcode.gethired.id/activity-groups?email=muhammadfaizalghozi1@gmail.com`;
      let respon = await axios.get(url);
      setLoading(false);
      setArtikels(respon.data.data);
    } catch (err) {
      setLoading(false);
      console.log(err);
    }
  };
  React.useEffect(() => {
    getArtikel();
  }, []);
  let vh = "";
  if (artikels.length === 0) {
    vh = "100";
  } else if (artikels.length <= 4) {
    vh = "115";
  } else {
    vh = "150";
  }
  return (
    <React.Fragment>
      <div className="bg-gray-100 " style={{ height: `${vh}vh` }}>
        <Title getArtikel={getArtikel}></Title>
        <div className="py-10">
          {loading ? (
            "loading"
          ) : (
            <React.Fragment>
              {artikels.length === 0 ? (
                <React.Fragment>
                  <div className="flex items-center justify-center">
                    <img
                      data-cy="activity-empty-state"
                      style={{ height: "450px" }}
                      className="mr-32"
                      src={cowo}
                      alt=""
                    />
                  </div>
                </React.Fragment>
              ) : (
                <React.Fragment>
                  <div className="grid grid-cols-4 gap-8 px-36">
                    {artikels.map((i, key) => (
                      <List
                        key={key}
                        artikels={artikels}
                        title={i.title}
                        id={i.id}
                        date={i.created_at}
                        getArtikel={getArtikel}
                      ></List>
                    ))}
                  </div>
                </React.Fragment>
              )}
            </React.Fragment>
          )}
        </div>
      </div>
    </React.Fragment>
  );
}
