import React, { useCallback, useState } from "react";
import List from "./list";
import cewe from "../../assets/cewe.png";
import Title from "./title/title";
import { useParams } from "react-router";
import axios from "axios";
import AddActivity from "../../popup/addActivity";

export default function Activity() {
  const [sort, setSort] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  let { id } = useParams();
  let { title } = useParams();
  const [modal, setModal] = React.useState(false);
  const [artikels, setArtikels] = React.useState([]);
  const [sortTitle, setSortTitle] = React.useState(false);
  const [reverse, setReverse] = React.useState(false);
  const [newest, setNewest] = useState(false);
  const [latest, setLatest] = useState(false);

  const getArtikel = useCallback(async () => {
    setLoading(true);
    try {
      let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
      let respon = await axios.get(url);
      setArtikels(respon.data.todo_items);
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  }, []);

  const getAz = React.useCallback(async () => {
    setLoading(true);
    try {
      let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
      let respon = await axios.get(url);
      setArtikels(
        respon.data.todo_items.sort(function (a, b) {
          var nameA = a.title.toUpperCase();
          var nameB = b.title.toUpperCase();
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          return 0;
        })
      );
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  }, [sortTitle]);

  const getReverse = React.useCallback(async () => {
    setLoading(true);
    try {
      let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
      let respon = await axios.get(url);
      setArtikels(
        respon.data.todo_items.sort((a, b) =>
          a.title.toLowerCase() > b.title.toLowerCase() ? -1 : 1
        )
      );
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  }, [reverse]);

  const getNewest = React.useCallback(async () => {
    setLoading(true);
    try {
      let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
      let respon = await axios.get(url);
      setArtikels(
        respon.data.todo_items.sort(
          (firstItem, secondItem) => firstItem.id + secondItem.id
        )
      );
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  }, [reverse]);

  const getLatest = React.useCallback(async () => {
    setLoading(true);
    try {
      let url = `https://todo.api.devcode.gethired.id/activity-groups/${id}`;
      let respon = await axios.get(url);
      setArtikels(
        respon.data.todo_items.sort(
          (firstItem, secondItem) => firstItem.id - secondItem.id
        )
      );
      setLoading(false);
    } catch (err) {
      console.log(err);
      setLoading(false);
    }
  }, [reverse]);
  

  React.useEffect(() => {
    if (sortTitle) {
      getAz();
    } else if (reverse) {
      getReverse();
    } else if (newest) {
      getNewest();
    } else if (latest) {
      getLatest();
    } else {
      getArtikel();
    }
  }, []);

  return (
    <React.Fragment>
        <div
          style={{ height: "87vh" }}
          className="bg-gray-50 overflow-x-auto h-screen"
        >
          <Title
            newest={newest}
            latest={latest}
            getLatest={getLatest}
            getNewest={getNewest}
            setLatest={setLatest}
            setNewest={setNewest}
            getReverse={getReverse}
            reverse={reverse}
            setReverse={setReverse}
            getAz={getAz}
            sortTitle={sortTitle}
            setSortTitle={setSortTitle}
            sort={sort}
            setSort={setSort}
            title={title}
            modal={modal}
            setModal={setModal}
          ></Title>
          <div className="px-28 mt-11 mb-10">
            {loading ? (
              "loading"
            ) : artikels.length === 0 ? (
              <React.Fragment>
                <div
                  onClick={() => {
                    setSort(false);
                  }}
                  className="flex items-center justify-center"
                >
                  <img data-cy="todo-empty-state" src={cewe} alt="" />
                </div>
              </React.Fragment>
            ) : (
              artikels.map((i, key) => (
                <List
                  sort={sort}
                  setSort={setSort}
                  key={key}
                  title={i.title}
                  active={i.is_active}
                  id={i.id}
                  priority={i.priority}
                  getArtikel={getArtikel}
                ></List>
              ))
            )}
          </div>
        </div>
        {modal ? (
          <React.Fragment>
            <AddActivity
              getArtikel={getArtikel}
              setModal={setModal}
              modal={modal}
            ></AddActivity>
          </React.Fragment>
        ) : (
          ""
        )}
    </React.Fragment>
  );
}
