import axios from "axios";
import React, { useState } from "react";
import { useHistory } from "react-router";

export default function Title(props) {
  let history = useHistory();
  const [upload, setUpload] = React.useState({
    title: "New Activity",
    email: "muhammadfaizalghozi1@gmail.com",
    _comment:
      "email digunakan untuk membedakan list data yang digunakan antar aplikasi",
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    let url = `https://todo.api.devcode.gethired.id/activity-groups`;
    try {
      await axios.post(url, upload);
      props.getArtikel()
    } catch (err) {
      console.log(err);
    }
  };
  return (
    <React.Fragment>
      <div className="flex popin py-7">
        <p data-cy="activity-title" className="capitalize font-bold py-7 px-36 text-4xl">activity</p>
        <div
          data-cy="activity-add-button"
          style={{ marginLeft: "54%" }}
          className="flex mt-5"
        >
          <button
            type="submit"
            onClick={handleSubmit}
            data-cy="activity-add-button"
            className=" flex space-x-3  items-center justify-between px-6 bg-blue-450 col-start-5 h-16 w-48 rounded-full"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-9 w-9 stroke-2 text-white"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M12 4v16m8-8H4"
              />
            </svg>
            <p className="text-2xl font-semibold text-white ">Tambah</p>
          </button>
        </div>
      </div>
    </React.Fragment>
  );
}
